		<section class="content card-company" :class="!isCreatePage ? 'content--masked' : ''">
			<div class="content_block content_block-card">
				<div class="content_section">

					<div class="flex_block">
						<span
							class="add_positionk add_positionk--no-action"
						>
							О компании
							<span class="card-company__address">г. Москва, ул. Московская, д. 1</span>
						</span>

						<div class="language_razdel">
							<svg class="icon icon--copy">
